@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400&display=swap);
*,*::after,*::before{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html{
    font-size: 62.5%;
    font-family: 'Poppins', sans-serif;
}

:root{
    --primaryBC : rgb(30, 32, 34);
    --secondaryBC : #22262F;
    --primaryFC : #e0e0e0;
    --iconsOp : rgba(224, 224, 224,0.6);
    --smallFS : 1.5em;
    --mediumFS : 1.8em;
    --largeFS : 2.5em;
    --testing : 1px solid white;
    --borderRa1 : 2em;
}

body{
    width: 100vw;
    height: 100vh;
    background-color: #2B3AF6;
    display: grid;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

h2{
    font-size: 2.5em;
    font-weight: 400;
}

I{
    font-size: 2em;
    color :rgba(224, 224, 224,0.6);
    color :var(--iconsOp);
    cursor: pointer;
    transition: color 0.3s;
}

I:hover{
    color : #e0e0e0;
}

.sm{
    font-size: 1.8em;
}

p{
    font-size: 1.5em;
    font-size: var(--smallFS);
    color : #e0e0e0;
    color : var(--primaryFC);
}

::selection{
    color: white;
}

main{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    background-color: rgb(30, 32, 34);
    background-color: var(--primaryBC);
    width: 40em;
    height: 60em;
    border-radius: 2em;
    border-radius: var(--borderRa1);
    box-shadow: 10px 20px 25px rgba(0, 0, 0, .2);
    position: relative;
}

.container{
    margin : 2em;
    /* border : var(--testing); */
    width: 90%;
    height: 90%;
    border-radius: calc(2em/2);
    border-radius: calc(var(--borderRa1)/2);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    grid-gap: 2em;
    gap: 2em;
    color : #e0e0e0;
    color : var(--primaryFC);
}

::-webkit-scrollbar{
    display: none;
    visibility: hidden;
}

.top{
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    /* border :var(--testing); */
    width: 100%;
    height: 10%;
}

@-webkit-keyframes fadein{
    0%{
        -webkit-transform: scale(0);
                transform: scale(0);
        opacity: 0;
    }

    100%{
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 1;
    }
}

@keyframes fadein{
    0%{
        -webkit-transform: scale(0);
                transform: scale(0);
        opacity: 0;
    }

    100%{
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 1;
    }
}

.contacts{
    height: 90%;
    overflow-y: scroll;
    scrollbar-width: none;
}

.list{
    --dflex : flex;
    --flexDir : column;
    --flexalign : center;
    --flexjustifySb : space-between;
    --flexgap : 1em; 
    --maxwidth : 100%;
    --minwidth : 70%;
    --flexpad : 1em;
    --minheight : 8em;
    --maxheight : 100%;
}

.contacts{
    display: var(--dflex);
    -webkit-flex-direction: var(--flexDir);
            flex-direction: var(--flexDir);
    -webkit-align-items: var(--flexalign);
            align-items: var(--flexalign);
    grid-gap: var(--flexgap);
    gap: var(--flexgap);
}

.contact{
    cursor: pointer;
}

.list .contact{
    display: var(--dflex);
    -webkit-align-items: var(--flexalign);
            align-items: var(--flexalign);
    -webkit-justify-content: var(--flexjustifySb);
            justify-content: var(--flexjustifySb);
    width: var(--maxwidth);
    padding: var(--flexpad);
    height: var(--minheight);
    /* border : var(--testing); */
    background-color: #22262F;
    background-color: var(--secondaryBC);
    border-radius: calc(2em/2);
    border-radius: calc(var(--borderRa1)/2);
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    opacity: 0;
    -webkit-animation: movein 0.5s linear forwards;
            animation: movein 0.5s linear forwards;
    transition: 0.5s;
    margin: 1em 0;
}

.list .contact:nth-child(1){
    margin-top :0;
}

.list .contact__left{
    display: var(--dflex);
    width: var(--minwidth);
    height: var(--maxwidth);
    -webkit-align-items : var(--flexalign);
            align-items : var(--flexalign);
}

.grid{
    --dgrid : grid;
    --gridTc1 : 1fr 1fr;
    --gridTr1 : repeat(4,13em);
    --gridgap : 1em;
    --gridpad : 1em;
    --gridTc2 : 85% 15%;
}

.contacts{
    display: var(--dgrid);
    grid-template-columns: var(--gridTc1);
    grid-template-rows: var(--gridTr1);
    grid-gap: var(--gridgap);
}

.grid .contact{
    display: var(--dgrid);
    grid-template-columns: var(--gridTc2);
    padding: var(--gridgap);
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    opacity: 0;
    border-radius: calc(2em/2);
    border-radius: calc(var(--borderRa1)/2);
    background-color: #22262F;
    background-color: var(--secondaryBC);
    -webkit-animation: movein 0.5s linear forwards;
            animation: movein 0.5s linear forwards;
    transition: 0.5s;
}

@-webkit-keyframes movein{
    0%{
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
        opacity: 0;
    }

    100%{
        -webkit-transform: translateX(0%);
                transform: translateX(0%);
        opacity: 1;
    }
}

@keyframes movein{
    0%{
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
        opacity: 0;
    }

    100%{
        -webkit-transform: translateX(0%);
                transform: translateX(0%);
        opacity: 1;
    }
}

img{
    height: 5em;
    width: 5em;
    margin-right: 1.5em;
}

.contact__left__details__name{
    font-size: 1.8em;
    font-size: var(--mediumFS);
    font-weight: 300;
}

.contact__left__details__phone{
    font-size: 1.5em;
    font-size: var(--smallFS);
    font-weight: 200;
}

.contact:nth-child(2){
    -webkit-animation-delay: 0.1s;
            animation-delay: 0.1s;
}

.contact:nth-child(3){
    -webkit-animation-delay: 0.2s;
            animation-delay: 0.2s;
}

.contact:nth-child(4){
    -webkit-animation-delay: 0.3s;
            animation-delay: 0.3s;
}

.contact:nth-child(5){
    -webkit-animation-delay: 0.4s;
            animation-delay: 0.4s;
}

.contact:nth-child(6){
    -webkit-animation-delay: 0.5s;
            animation-delay: 0.5s;
}

.contact:nth-child(7){
    -webkit-animation-delay: 0.6s;
            animation-delay: 0.6s;
}

.overlay{
    position: absolute;
    width: 100%;
    height: 100%;
    top : 0%;
    left : 0%;
    background-color: rgba(0, 0, 0, .4);
    z-index : 1;
    border-radius: calc(2em/2);
    border-radius: calc(var(--borderRa1)/2);
    display: none;
}

.modal{
    position: absolute;
    width: 80%;
    height: 30%;
    top : 30%;
    left : 10%;
    -webkit-transform: scale(0);
            transform: scale(0);
    background-color: rgb(30, 32, 34);
    background-color: var(--primaryBC);
    z-index: 2;
    border-radius: calc(2em/2);
    border-radius: calc(var(--borderRa1)/2);
    display: none;
    padding : 1.5em 1em 1em 1em;
    box-shadow: 0px 0px 40px rgba(0, 0, 0, .2);
}

.openmodal.modal{
    display: block;
    -webkit-animation: scaleup 0.5s ease-in 0.1s forwards;
            animation: scaleup 0.5s ease-in 0.1s forwards;
}

.submodal{
    /* border : var(--testing); */
    width: 100%;
    height : 100%;
    display : -webkit-flex;
    display : flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items : center;
            align-items : center;
    -webkit-justify-content : space-evenly;
            justify-content : space-evenly;
}

.submodal h3{
    font-size: 1.8em;
    font-size: var(--mediumFS);
    text-align: center;
}

@-webkit-keyframes scaleup {
    to{
        -webkit-transform : scale(1);
                transform : scale(1);
    }
}

@keyframes scaleup {
    to{
        -webkit-transform : scale(1);
                transform : scale(1);
    }
}


.add-contact{
    position: absolute;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    width: 80%;
    height: 70%;
    bottom : 4%;
    right : 4%;
    background-color: rgb(30, 32, 34);
    background-color: var(--primaryBC);
    /* border : var(--testing); */
    border-radius: calc(2em/2);
    border-radius: calc(var(--borderRa1)/2);
    -webkit-clip-path: circle(8% at 90% 93%);
            clip-path: circle(8% at 90% 93%);
    transition: 0.5s ease-in;
    z-index: 10;
    box-shadow: 0px 10px 30px rgba(0, 0, 0, .6);
    padding : 1em;
}

.add-contact-icon{
    position: absolute;
    bottom : 4%;
