            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    font-size: 3em;
    color : rgba(224, 224, 224,0.6);
    color : var(--iconsOp);
    background-color: #22262F;
    background-color: var(--secondaryBC);
    /* border : var(--testing); */
    padding : .6em;
    border-radius: 50%;
    z-index: 12;
    box-shadow: 0px 10px 30px rgba(0, 0, 0, .6);
    transition: 0.5s;
}

.add-contact.open{
    -webkit-transform : translate(-7.5%,-10%);
            transform : translate(-7.5%,-10%);
    -webkit-clip-path : circle(100%);
            clip-path : circle(100%);
    box-shadow: 0px 10px 30px rgba(0, 0, 0, .4);
}

